import Vue from 'vue'
import Vuex from 'vuex'

import cart from './cart'
import address from './address'
import order from './order'
import list from './list'
import search from './search'
import home from './home'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    vantTab: true,
    num: 0,
    // 储存提交的订单数据
    tobepaidlist: {},
    // 数据
    list: list,
    // 喜欢收藏列表
    likelist: [],
    // 足迹列表
    footprintlist: [],
    // 支付密码
    Paymentpassword: "",
    // 登入状态 
    login: 0,
    // 用户名
    username: null,
    // 用户出生日期
    dataTime: null,
  },
  mutations: {
    // 收藏列表增加 商品改为喜欢
    modifylike(state, id) {
      // console.log(id)
      let index = state.list.findIndex(val => val.id === id);
      // console.log(index)
      state.list[index].like = true;
      state.likelist.push(state.list[index])
    },
    // 商品改为不喜欢
    Nolike(state, id) {
      let index = state.likelist.findIndex(val => val.id === id);
      state.likelist.splice(index, 1)
      // console.log(state.likelist)
      state.list[index].like = false;
    },
    // 删除不喜欢的商品
    del(state, index) {
      state.likelist.splice(index, 1)
    },
    // 足迹列表增加
    addfootprint(state, obj) {
      state.footprintlist.push(obj)
    }
  },
  actions: {
  },
  modules: {
    cart,   //详情 与 购物车
    address, //地址
    order,   //订单
    search,  //搜索
    home,    //首页及首页二级页面
  },

})
