export default [
    {
        path: "/guidepage",
        name: "guidepage",
        component: () => import("../../components/public/guidepage.vue")
    },
    {
        path: "/advertpage",
        name: "advertpage",
        component: () => import("../../components/public/advertpage.vue"),
        // 独享路由守卫
        beforeEnter: (to, from, next) => {
            // 1.通过存储过程，判断存储中是否有数据
            // localStorage 关闭浏览器数据存在，只有清除数据缓存才消失
            if (localStorage.advertpages) {
                // 1.有数据，进入广告页
                next();
            } else {
                // 2.没有数据，进入引导页
                localStorage.advertpages = true;
                next("/guidepage");
            }
        }
    }
]