export default {
    namespaced: true,//命名空间
    state: {
        // 订单完整性不可以修改
        lists: [

        //    {
        //     //订单固定id
        //        orderID:"202001016204",
        //     // 地址
        //     address:{

        //     },
        //     // 商品列表
        //        goodlist:[

        //        ],
        //     // 状态
        //     type:1, 1 待支付 2待收货 3，已收货
        //    },
        ],
    typelist: ["全部订单", "待支付", "待收货", "已收货"]
},
mutations: {
    // 增加
    add(state, obj){
        state.lists.push(obj)
        // console.log(state.lists)
    },
    // 修改状态
    confirm(state,index){
        state.lists[index].type = 3
    },
    // 删除
    del(state,index){
        state.lists.splice(index,1)
    },
    // 修改状态
    payment(state,index){
        state.lists[index].type = 2
    },
    // 删除
    Del(state,item){
        let index = state.lists.findIndex(val => val.time == item.time);
        state.lists.splice(index,1)
    },
    // 修改状态
    Confirm(state,item){
        let index = state.lists.findIndex(val => val.time == item.time);
        state.lists[index].type = 3
    },
    // 修改状态
    Payment(state,item){
        let index = state.lists.findIndex(val => val.time == item.time);
        state.lists[index].type = 2
    }
},
actions: { },
getters: {

},
}