export default {
    namespaced: true,//命名空间
    state: {
        chosenAddressId: "1",
        lists: [
            // {
            //     id: "1",
            //     name: "张三",
            //     tel: "13000000000",
            //     address: "浙江省杭州市西湖区文三路 138 号东方通信大厦 7 楼 501 室",
            //     province: "浙江省",
            //     city: "杭州市",
            //     county: "西湖区",
            //     areaCode: "330106",
            //     addressDetail: "文三路 138 号东方通信大厦 7 楼 501 室",
            //     isDefault: true,
            // },
            // {
            //     id: "2",
            //     name: "张三2",
            //     tel: "13000000000",
            //     address: "浙江省杭州市西湖区文三路 138 号东方通信大厦 7 楼 501 室",
            //     province: "浙江省",
            //     city: "杭州市",
            //     county: "西湖区",
            //     areaCode: "330106",
            //     addressDetail: "文三路 138 号东方通信大厦 7 楼 501 室",
            //     isDefault: false,
            // },
        ],
    },
    mutations: {
        // 增加数据
        add(state, obj) {
            obj.id = (state.lists.length + 1) + "";
            if (state.lists.length < 1) {
                obj.isDefault = true;
            }
            state.lists.push(obj); //添加地址
            // console.log(state.lists)
        },
        // 数据更新
        edit(state, obj) {
            Object.assign(state.lists, obj);
        },
        // 修改数据
        editid(state, obj) {
            for(let i in obj){
                state.lists[obj.index][i] = obj[i]
            }
            delete state.lists[obj.index].index
        },
        //删除
        del(state,obj){
            state.lists.splice(obj.index,1)
        }
    },
    actions: {},
    getters: {

    },
}