export default {
    namespaced: true,//命名空间
    state: {
        list: ["电脑","手机"]
    },
    mutations: {
        add(state, obj) {
            if (state.list.length == 10) {
                state.list.splice(0, 1)
            }
            for(let i=0;i<state.list.length;i++){
                if(state.list[i] == obj){
                    return;
                }
            }
            state.list.push(obj)
            // console.log(state.list)
        },
        del(state){
            state.list=[]
        }
    },
    actions: {},
    getters: {

    },
}